"use client";

import React, { useEffect, useState, useRef } from "react";
import { useRouter } from "next/navigation";
import { OrdersTable } from "./components/orders-table";
import { getOrders } from "./api";
import { Order } from "./types";
import { toast } from "sonner";
import { getColumns } from "./columns";
import { AddOrderDialog } from "./components/add-order-dialog";
import { Button } from "@/components/ui/button";
import { StatusDropdownProps } from "./types";

// ---------------- Status Multi-Select Dropdown ----------------
const StatusDropdown: React.FC<StatusDropdownProps> = ({
  options,
  selected,
  onChange,
}) => {
  const [open, setOpen] = useState(false);
  const [search, setSearch] = useState("");
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (ref.current && !ref.current.contains(event.target as Node)) {
        setOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  const toggleOption = (option: string) => {
    if (selected.includes(option)) {
      onChange(selected.filter((s) => s !== option));
    } else {
      onChange([...selected, option]);
    }
  };

  const filteredOptions = options.filter((o) =>
    o.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="relative inline-block" ref={ref}>
      <button
        type="button"
        className="border rounded px-3 py-1 text-sm w-52 text-left"
        onClick={() => setOpen(!open)}
      >
        {selected.length > 0 ? selected.join(", ") : "Select status"}
      </button>

      {open && (
        <div className="absolute z-10 mt-1 w-52 bg-white border rounded shadow-lg max-h-60 overflow-auto p-2">
          <input
            type="text"
            placeholder="Filter status..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="w-full mb-2 border rounded px-2 py-1 text-sm"
          />
          {filteredOptions.map((option) => (
            <label
              key={option}
              className="flex items-center gap-2 px-2 py-1 hover:bg-gray-100 cursor-pointer"
            >
              <input
                type="checkbox"
                checked={selected.includes(option)}
                onChange={() => toggleOption(option)}
              />
              {option}
            </label>
          ))}
          {filteredOptions.length === 0 && (
            <p className="text-xs text-gray-400 px-2 py-1">
              No matching status
            </p>
          )}
        </div>
      )}
    </div>
  );
};

// ---------------- Orders Page ----------------
export default function OrdersPage() {
  const router = useRouter();

  // âœ… CLIENT GUARD: redirect if token missing
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      router.replace("/login");
    }
  }, [router]);

  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);
  const [addDialogOpen, setAddDialogOpen] = useState(false);

  // Filters
  const [searchTerm, setSearchTerm] = useState("");
  const [customerFilter, setCustomerFilter] = useState("");
  const [statusFilter, setStatusFilter] = useState<string[]>([]);

  const [sortField, setSortField] = useState("createdAt");
  const [sortDir, setSortDir] = useState<"asc" | "desc">("desc");

  // ---------------- Logout ----------------
  const logout = () => {
    localStorage.removeItem("token");
    router.replace("/login");
  };

  // ---------------- Fetch Orders ----------------
  const fetchOrders = async (
    pageNumber = page,
    size = pageSize,
    search = searchTerm,
    customer = customerFilter,
    statuses = statusFilter,
    sortBy = sortField,
    sortDirection = sortDir
  ) => {
    setLoading(true);
    try {
      const res = await getOrders(
        pageNumber,
        size,
        search,
        customer,
        statuses,
        sortBy,
        sortDirection
      );
      setOrders(res.data.items);
      setTotal(res.data.total);
      setPage(res.data.page);
      setPageSize(size);
    } catch (err) {
      toast.error("Failed to fetch orders");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOrders(page, pageSize);
  }, [
    page,
    pageSize,
    searchTerm,
    customerFilter,
    statusFilter,
    sortField,
    sortDir,
  ]);

  const clearFilters = () => {
    setSearchTerm("");
    setCustomerFilter("");
    setStatusFilter([]);
  };

  return (
    <div className="p-4 space-y-4">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Orders</h1>

        <div className="flex items-center gap-2">
          <Button variant="outline" onClick={logout}>
            Logout
          </Button>
          <Button onClick={() => setAddDialogOpen(true)}>Add Order</Button>
        </div>
      </div>

      {/* Filters */}
      <div className="flex flex-wrap gap-2 items-center">
        <input
          className="border rounded px-2 py-1 text-sm"
          placeholder="Global search..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <input
          className="border rounded px-2 py-1 text-sm"
          placeholder="Customer filter..."
          value={customerFilter}
          onChange={(e) => setCustomerFilter(e.target.value)}
        />
        <StatusDropdown
          options={["OPEN", "PENDING", "INPROGRESS", "COMPLETED", "CANCELLED"]}
          selected={statusFilter}
          onChange={setStatusFilter}
        />
        <Button size="sm" variant="outline" onClick={clearFilters}>
          Clear Filters
        </Button>
      </div>

      {/* Add Order Dialog */}
      <AddOrderDialog
        open={addDialogOpen}
        onClose={() => setAddDialogOpen(false)}
        refreshData={() => fetchOrders(1, pageSize)}
      />

      {/* Orders Table */}
      <OrdersTable
        data={orders}
        page={page}
        pageSize={pageSize}
        total={total}
        loading={loading}
        onPageChange={setPage}
        refreshData={fetchOrders}
        columns={getColumns(fetchOrders)}
        sortField={sortField}
        sortDir={sortDir}
        setSortField={setSortField}
        setSortDir={setSortDir}
      />
    </div>
  );
}
