import axios from "axios";
import { Order, OrdersResponse } from "./types";

/* ================================
   Axios Instance
================================ */
const api = axios.create({
  baseURL: `${process.env.NEXT_PUBLIC_API_URL}/orders`, // use env variable
  headers: {
    "Content-Type": "application/json",
  },
});

// âœ… Attach token to every request
api.interceptors.request.use(
  (config) => {
    if (typeof window !== "undefined") {
      const token = localStorage.getItem("token");
      if (token) {
        config.headers.Authorization = `Bearer ${token}`;
      }
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// âœ… Global response interceptor to handle invalid/expired token
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (
      error.response?.status === 401 || // Unauthorized
      error.response?.status === 403    // Forbidden
    ) {
      if (typeof window !== "undefined") {
        localStorage.removeItem("token");
        window.location.href = "/login"; // redirect to login
      }
    }
    return Promise.reject(error);
  }
);

/* ================================
   API Functions
================================ */
export const getOrders = async (
  page: number = 1,
  pageSize: number = 10,
  search?: string,
  customer?: string,
  statuses?: string[],
  sortBy?: string,
  sortDirection?: "asc" | "desc"
): Promise<{ data: OrdersResponse }> => {
  const params: Record<string, any> = { page, pageSize };
  if (search) params.search = search;
  if (customer) params.customerName = customer;
  if (statuses && statuses.length > 0) params["status[]"] = statuses;
  if (sortBy) params.sortBy = sortBy;
  if (sortDirection) params.sortDirection = sortDirection;

  return api.get<OrdersResponse>("", { params });
};

export const getNextOrderNo = async (): Promise<string> => {
  const res = await api.get<{ orderNo: string }>("/next-order-no");
  return res.data.orderNo;
};

export const createOrder = (data: {
  orderNo: string;
  customerName: string;
  amount: number;
  status: string;
}) => api.post("", data);

export const updateOrder = (
  id: number,
  data: { customerName: string; amount: number; status: string }
) => api.put(`/${id}`, data);

export const deleteOrder = (id: number) => api.delete(`/${id}`);

export default api;
