"use client";

import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { createOrder, getNextOrderNo } from "../api";
import { orderSchema, OrderFormData } from "./validation";
import { AddOrderDialogProps } from "../types";
//import { z } from "zod";



export function AddOrderDialog({
  open,
  onClose,
  refreshData,
}: AddOrderDialogProps) {
  const [orderNo, setOrderNo] = useState("");
  const [form, setForm] = useState<OrderFormData>({
    customerName: "",
    amount: 0,
    status: "OPEN",
  });

  const [errors, setErrors] = useState<
    Partial<Record<keyof OrderFormData, string>>
  >({});
  const [loading, setLoading] = useState(false);

  // ðŸ”¹ Fetch next serial order number when dialog opens
  useEffect(() => {
    if (open) {
      getNextOrderNo()
        .then((res) => setOrderNo(res))
        .catch(() => {
          toast.error("Failed to get order number");
          setOrderNo("");
        });
    }
  }, [open]);

  const handleSave = async () => {
    setErrors({});

    // ðŸ”¹ Frontend validation using Zod
    const result = orderSchema.safeParse(form);

    if (!result.success) {
      const fieldErrors: Partial<Record<keyof OrderFormData, string>> = {};
      result.error.issues.forEach((issue) => {
        fieldErrors[issue.path[0] as keyof OrderFormData] = issue.message;
      });
      setErrors(fieldErrors);
      return;
    }

    try {
      setLoading(true);

      await createOrder({
        orderNo, // serial number from backend
        ...form,
      });

      toast.success(`Order ${orderNo} created successfully`);
      onClose();
      refreshData();

      // Reset form
      setForm({
        customerName: "",
        amount: 0,
        status: "OPEN",
      });
    } catch {
      toast.error("Failed to create order");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Add Order</DialogTitle>
        </DialogHeader>

        {/* <div className="space-y-3 mt-2"> */}
        <div className="flex flex-col gap-2 mt-2">
          {/* Order No (readonly) */}
          <Input value={orderNo} readOnly placeholder="Order No" />

          {/* Customer Name */}
          <Input
            placeholder="Customer Name"
            value={form.customerName}
            onChange={(e) =>
              setForm({ ...form, customerName: e.target.value })
            }
          />
          {errors.customerName && (
            <p className="text-sm text-red-500">{errors.customerName}</p>
          )}

          {/* Amount */}
          <Input
            type="number"
            placeholder="Amount"
            value={form.amount || ""}
            onChange={(e) =>
              setForm({ ...form, amount: Number(e.target.value) })
            }
          />
          {errors.amount && (
            <p className="text-sm text-red-500">{errors.amount}</p>
          )}

          {/* Status */}
          <select
            className="border rounded-md p-2"
            value={form.status}
            onChange={(e) =>
              setForm({
                ...form,
                status: e.target.value as OrderFormData["status"],
              })
            }
          >
            <option value="OPEN">OPEN</option>
            <option value="PENDING">PENDING</option>
            <option value="INPROGRESS">INPROGRESS</option>
            <option value="COMPLETED">COMPLETED</option>
            <option value="CANCELLED">CANCELLED</option>
          </select>
        </div>

        <Button
          className="w-full mt-4"
          onClick={handleSave}
          disabled={loading || !orderNo}
        >
          {loading ? "Saving..." : "Save"}
        </Button>
      </DialogContent>
    </Dialog>
  );
}
